<template>
  <v-sheet :color="color" class=" mb-3 mx-3 " width="100%">
    <v-toolbar class="mb-3" dense flat :color="color">
      <v-toolbar-title class="title ">{{title}}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn outlined class="mx-2" @click="$emit('table', 'sale')">Sales</v-btn>
      <v-btn outlined class="mx-2" @click="$emit('table', 'retention')">Retentions</v-btn>
    </v-toolbar>
  

    <v-row class="mx-1">
      <v-col class="mx-2 pa-0 px-2" v-for="(item, ind) in (selected == 'month' ? items : items2)" :key="ind">
         
        <fieldset class="px-2 mb-2 fielset">
          <legend>{{ item.title }}</legend>
          
            <v-btn v-if="item.table" icon @click="$emit('table', item.table)"><v-icon>mdi-table-multiple</v-icon></v-btn>
          <v-row>
            <v-col cols="3"><small>Users</small><h2>{{ item.ftd || 0 }}</h2></v-col>
            <v-col> <small>Total</small><h2>{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.total || 0)}}</h2></v-col>
          </v-row>
          </fieldset>
      </v-col>  
    </v-row>
  </v-sheet>
</template>
<script>
 
export default {
  name: 'TotalSheet',
  props: {
    title: String,
    items: Array,  
    items2: Array,  
    color: {
      type: String,
      default: 'green lighten-3'
    }
  },
  data: () => ({
    selected: 'month'
  }) 
}
</script>
<style   scoped>
.fielset {
  border: 1px solid gray;
  border-radius: 5px;
}
</style>